<template>
  <div
    class="row item_box product_card"
    v-bind:class="{
      mobile_dragclass: !draggable,
      borderClass: !draggable,
    }"
  >
    <!-- {{list.pos}} -->
    <v-avatar class="mobileview" tile v-if="list.imageurl">
      <img
        class="mobileview justify-start"
        alt="image"
        :src="list.imageurl"
        style="width: 70px; height: 70px"
      />
    </v-avatar>

    <div class="viewform_images_list">
      <img
        class="desktopView justify-start item-box"
        style="width: 100%; object-fit: cover;"
        v-if="list.imageurl"
        alt="image"
        :src="list.imageurl"
      />
      <div v-else>
        <img src="../assets/img/no_preview.png" alt="no_preview" />
        <p>{{ $t("newUi.adminViewform.noPreview") }}</p>
      </div>
    </div>
    <!-- </v-avatar> -->
    <div class="desktopView" style="font-size: 12px; padding: 5px; width: 100%">
      <div class="d-flex justify-space-between">
        <div
          class="justify-start item_text"
          :class="{ dragable_color: !draggable }"
        >
          {{ list.name }}
        </div>
        <div class="justify-end justify-content-end" style="font-size; 18px;">
          <v-menu bottom left>
            <template v-slot:activator="{ on, attrs }">
              <span
                v-bind="attrs"
                v-on="on"
                v-bind:class="{
                  displayNone: !draggable,
                }"
                class="text-center cursor-pointer item-edit-icon"
                style="font-size: 16px; font-weight: bold"
                >...</span
              >
            </template>

            <v-list>
              <v-list-item>
                <v-list-item-title
                  class="cursor-pointer"
                  style="padding: 5px"
                  @click="edit_product_desktop"
                >
                  {{ $t("newUi.adminViewform.edit") }}
                </v-list-item-title>
              </v-list-item>
              <v-list-item>
                <v-list-item-title
                  class="cursor-pointer"
                  style="padding: 5px"
                  @click="delete_product_dialog = true"
                >
                  {{ $t("newUi.adminViewform.delete") }}
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
      </div>
      <div class="d-flex justify-space-between">
        <read-more
          content-class="text_body"
          :class="{ dragable_color: !draggable }"
          style="word-break: break-word"
          :more-str="$t('newUi.adminViewform.readMore')"
          :text="list.desc"
          link="#"
          :less-str="$t('newUi.adminViewform.readLess')"
          :max-chars="20"
        ></read-more>
        <img
          v-if="!draggable"
          :class="{ dragable_color: !draggable }"
          @mouseover="dragCategory"
          @mouseout="stopDrag"
          src="../assets/ui/drag_icon.svg"
          style="cursor: grab; align-self: flex-start"
        />
      </div>
      <div class="item_text" :class="{ dragable_color: !draggable }">
        <span style="margin-right: 3px">{{
          this.$store.state.currentForm.symbol
        }}</span
        >{{ parseFloat(list.price).toFixed(2) }}
      </div>
    </div>
    <div
      class="col mobileview"
      style="
        padding-left: 10px;
        padding-top: 0px;
        padding-bottom: 0px;
        padding-right: 0px;
      "
    >
      <div class="d-flex justify-space-between">
        <div class="item_text" :class="{ dragable_color: !draggable }">
          <read-more
            content-class="text_body"
            :class="{ dragable_color: !draggable }"
            style="word-break: break-word"
            :more-str="$t('newUi.adminViewform.readMore')"
            :text="list.name"
            link="#"
            :less-str="$t('newUi.adminViewform.readLess')"
            :max-chars="20"
          ></read-more>
        </div>
        <div class="" style="align-self: flex-start">
          <v-menu bottom left>
            <template v-slot:activator="{ on, attrs }">
              <p
                v-bind="attrs"
                v-on="on"
                v-bind:class="{
                  displayNone: !draggable,
                }"
                class="text-center cursor-pointer item-edit-icon"
                style="
                  font-size: 16px;
                  font-weight: bold;
                  align-self: flex-start;
                "
              >
                ...
              </p>
            </template>

            <v-list>
              <v-list-item>
                <v-list-item-title
                  class="cursor-pointer"
                  style="padding: 5px"
                  @click="edit_product = true"
                >
                  {{ $t("newUi.adminViewform.edit") }}
                </v-list-item-title>
              </v-list-item>
              <v-list-item>
                <v-list-item-title
                  class="cursor-pointer"
                  style="padding: 5px"
                  @click="delete_product = true"
                >
                  {{ $t("newUi.adminViewform.delete") }}
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
      </div>
      <div class="d-flex justify-space-between">
        <read-more
          class="text_body"
          :class="{
            dragable_color: !draggable,
            dis_landscape: $vuetify.breakpoint.height < 450,
          }"
          :more-str="$t('newUi.adminViewform.readMore')"
          :text="list.desc"
          link="#"
          :less-str="$t('newUi.adminViewform.readLess')"
          :max-chars="20"
        ></read-more>
        <img
          v-if="!draggable"
          @click="dragCategory"
          :class="{ dragable_color: !draggable }"
          @mouseout="stopDrag"
          src="../assets/ui/drag_icon.svg"
          style="cursor: grab align-self: flex-start; height: 18px;"
        />
      </div>
      <div class="item_text" :class="{ dragable_color: !draggable }">
        <span style="margin-right: 3px">{{
          this.$store.state.currentForm.symbol
        }}</span
        >{{ list.price }}
      </div>
    </div>

    <v-bottom-sheet v-model="edit_product">
      <v-card class="elevation-0 bottom_modal">
        <v-card-text
          class="text-center "
          style="overflow-y: scroll;"
          :class="{
            sheet_height: $vuetify.breakpoint.height < 450,
            sheet_normal_height: $vuetify.breakpoint.height > 450,
          }"
        >
          <EditProducts
            :payload="list"
            :current_form_data="payload"
          ></EditProducts>
        </v-card-text>
      </v-card>
      <!-- <v-sheet class="text-center bottom_modal" height="90%">
        <EditProducts
          :payload="list"
          :current_form_data="payload"
        ></EditProducts>
      </v-sheet> -->
    </v-bottom-sheet>
    <v-bottom-sheet v-model="delete_product">
      <v-sheet class="text-center bottom_modal" height="300px">
        <div class="pt-8 text_color">
          {{ $t("newUi.deleteProduct.deletePro") }}
        </div>
        <v-container>
          <div class="col">
            {{ $t("newUi.deleteProduct.deleteProConfirm") }}
          </div>
          <div class="text-center mt-3">
            <v-btn
              @click="cancel_delete()"
              class="button_padding"
              color="#8AB26D"
            >
              {{ $t("newUi.deleteCategory.cancel") }}
            </v-btn>
            <v-btn
              @click="delete_product_submit(list.pos)"
              class="button_padding"
              color="#8AB26D"
            >
              {{ $t("newUi.deleteCategory.confirm") }}
            </v-btn>
            <!-- <div @click="add_category_more(name, description)" class="text-decoration-underline cursor text_color">save and add more</div> -->
          </div>
        </v-container>
      </v-sheet>
    </v-bottom-sheet>
    <v-dialog v-model="delete_product_dialog" content-class="delete_dialog">
      <v-container>
        <!-- <div class="d-flex justify-space-between text_color"> -->
        <div
          class="d-flex justify-end justify-content-end text_color"
          style="font-size: 14px"
        >
          <i
            class="bi bi-x cursor-pointer"
            @click="delete_product_dialog = false"
          ></i>
        </div>
        <!-- </div> -->
        <div class="text-center text_color">
          {{ $t("newUi.deleteProduct.deletePro") }}
        </div>
        <div class="col text-center">
          {{ $t("newUi.deleteProduct.deleteProConfirm") }}
        </div>
        <div class="text-center mt-1">
          <v-btn
            @click="delete_product_dialog = false"
            class="button_padding"
            color="#8AB26D"
          >
            {{ $t("newUi.deleteCategory.cancel") }}
          </v-btn>
          <v-btn
            @click="delete_product_submit(list.pos)"
            class="button_padding"
            color="#8AB26D"
          >
            {{ $t("newUi.deleteCategory.confirm") }}
          </v-btn>
          <!-- <div @click="add_category_more(name, description)" class="text-decoration-underline cursor text_color">save and add more</div> -->
        </div>
      </v-container>
    </v-dialog>
  </div>
</template>
<script>
import EditProducts from "@/components/EditProducts.vue";
import router from "../router/router";
import { saveFile, setLocale, loadFile } from "../services/firebase";
import { saveMasterData } from "../services/firebase";
import store from "../store/store";
import i18n from "../i18n";
export default {
  props: [
    "payload",
    "list",
    "draggable",
    "dragCategory",
    "stopDrag",
    "isMobile",
  ],
  components: {
    EditProducts,
  },
  data() {
    return {
      dialog: false,
      edit_product: false,
      edit_category: false,
      delete_product: false,
      delete_product_dialog: false,
      set_overlay: false,
      readMore: "#8ab26d",
    };
  },
  mounted() {
    setLocale();
  },
  methods: {
    cancel_delete: function() {
      this.delete_product = false;
    },
    fetchData: function() {
      var store = this.$store;
      store.dispatch("setOverlay", true);
      store.dispatch("setCurrentFormData", {
        id: store.state.profile.uid,
        pos: "",
        categs: ["Ordering form", "Catalogue"],
        shopname: "",
        shopdesc: "",
        type: "",
        catalogue: [],
        phone: "",
      });
      this.$store.state.currentFormPos = this.$route.params.formno;
      const disele = this;
      loadFile(
        disele.$store.state.profile.uid,
        disele.$route.params.formno,
        disele.$store.state,
        function(error) {
          if (error) {
            //TODO skeleton data
            console.log("error");
            store.dispatch("setCurrentFormData", {
              id: store.state.profile.uid,
              pos: store.state.currentFormPos,
              categs: ["Ordering form", "Catalogue"],
              shopname: "",
              shopdesc: "",
              type: "Ordering form",
              catalogue: [],
              phone: "+919000000000",
            });
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          } else {
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          }
        }
      );
    },
    delete_product_submit: function(pos) {
      console.log("delete_product_submit");
      console.log("this.props", this.props);
      this.delete_product_dialog = false;
      var form = this.$store.state.currentForm;
      for (var i = 0; i < form.catalogue.length; i++) {
        var curitem = form.catalogue[i];
        if (curitem.pos === pos) {
          form.catalogue.splice(i, 1);
          this.$store.state.currentForm = form;
          this.saveCurrentForm();
          this.delete_product = false;
          // router.go()
        }
      }
    },
    edit_product_desktop() {
      router.push({
        name: "addItems",
        params: {
          formno: this.$store.state.currentForm.pos,
          item_deatails: this.list,
          edit_type: "edit_product",
        },
      });
    },
    saveCurrentForm: function() {
      console.log("saveCurrentForm for dlt ");
      this.$store.dispatch("setOverlay", true);
      this.set_overlay = true;
      var elethis = this;
      saveFile(
        this.$store.state.profile.uid,
        this.$store.state.currentFormPos,
        JSON.stringify(this.$store.state.currentForm),
        function(error) {
          if (error) {
            //TODO skeleton data
            alert(i18n.t("alertText.saveFailed"));
            store.dispatch("setOverlay", false);
            elethis.set_overlay = false;
          } else {
            elethis.set_overlay = false;
            store.dispatch("setOverlay", false);
          }
        }
      ).then(() => {
        saveMasterData(function(err) {
          console.log(err);
        }, this.$store.state);
        this.fetchData();
      });
      console.log("delete", this.$store.state.currentForm);
    },
  },
};
</script>
