<template>
  <v-container
    class="mobile_side_padding"
    :class="{ landscape_side_padding: $vuetify.breakpoint.height < 450 }"
  >
    <div></div>
    <!-- <p v-if="draggableList">hello</p> -->
    <v-overlay :value="set_overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <draggable
      :animation="200"
      ghost-class="ghost-card"
      v-model="category_list_item"
      @end="categoryDragg"
      v-bind="{ disabled: draggableList }"
    >
      <transition-group name="fade">
        <div
          class="container"
          style="padding: 0px; margin-top: 3%"
          v-for="item in category_list_item"
          :key="item.pos + item.name + item.type"
        >
          <editDeleteCategory
            :item="item"
            :payload="payload"
            :isMobile="isMobile"
            :draggable="draggableList"
            :dragCat="dragCat"
            :dragCategory="dragCategory"
            :stopDrag="stopDrag"
          ></editDeleteCategory>
          <div class="grid-container2_n">
            <draggable
              :animation="200"
              ghost-class="ghost-card"
              group="items"
              v-model="item.items"
              @end="itemDraggg($event, item.items)"
              v-bind="{
                disabled: draggableList,
              }"
            >
              <transition-group name="fade" class="grid_n">
                <div
                  class="item_box"
                  v-for="list in item.items"
                  :key="list.pos"
                  style="margin-top: 3%"
                  v-bind:class="{
                    mobile_dragclass: !draggableList,
                    borderClass: !draggableList,
                  }"
                >
                  <itemList
                    :payload="$store.state.currentForm.catalogue"
                    :list="list"
                    :draggable="draggableList"
                    :dragCategory="dragCategory"
                    :isMobile="isMobile"
                    :stopDrag="stopDrag"
                  ></itemList>
                </div>
              </transition-group>
            </draggable>
          </div>
        </div>
      </transition-group>
    </draggable>
    <div
      class="container"
      style="padding: 0px; margin-top: 3%"
      v-show="no_category_list.length != 0 ? true:false"
    >
      <div class="item_category" style="">
        <div
          class="col text_bold"
          style="padding-top: 3px; padding-bottom: 2px"
        >
          {{ $t("newUi.adminViewform.noCat") }}
        </div>
      </div>
      <div class="grid-container2_n">
        <draggable
          :animation="200"
          ghost-class="ghost-card"
          group="items"
          v-model="no_category_list"
          @end="itemDraggnoCat($event, no_category_list)"
          v-bind="{
            disabled: draggableList,
          }"
        >
          <transition-group name="fade" class="grid_n">
            <div
              class="item_box"
              v-for="list in no_category_list"
              :key="list.pos"
              style="margin-top: 3%"
              v-bind:class="{
                mobile_dragclass: !draggableList,
                borderClass: !draggableList,
              }"
            >
              <itemList
                :payload="$store.state.currentForm.catalogue"
                :list="list"
                :draggable="draggableList"
                :dragCategory="dragCategory"
                :stopDrag="stopDrag"
              ></itemList>
            </div>
          </transition-group>
        </draggable>
      </div>
    </div>
  </v-container>
</template>
<script>
import draggable from "vuedraggable";
import ItemList from "./itemList.vue";
import router from "../router/router";
import { saveFile, setLocale } from "../services/firebase";
import { saveMasterData } from "../services/firebase";
import store from "../store/store";
import i18n from "../i18n";
import editDeleteCategory from "./editDeleteCategory";

export default {
  props: ["payload", "draggableList"],
  components: {
    draggable,
    ItemList,
    editDeleteCategory,
  },
  data() {
    var category_list = [];
    return {
      dialog: false,
      edit_product: false,
      edit_category: false,
      delete_category_show: "",
      no_category_list: [],
      category_list,
      dragging: false,
      disableDraggableCategory: this.isMobile ? this.draggableList : true,
      testdata: true,
      set_overlay: false,
    };
  },
  computed: {
    stateData: function() {
      return this.$store.state;
    },
    isMobile: function() {
      var x = window.matchMedia("(max-width: 480px)");
      if (x.matches) {
        return true;
      } // If media query matches
      else {
        return false;
      }
    },
    category_list_item() {
      return this.category_list;
    },
  },
  methods: {
    test: function() {
      this.testdata = "true";
    },
    dragCat: function() {
      this.disableDraggableCategory = !this.disableDraggableCategory;
    },
    dragCategory: function() {
      this.disableDraggableCategory = false;
    },
    stopDrag: function() {
      this.disableDraggableCategory = true;
    },
    category_lists: function() {
      var catName = "";
      for (var i = 0; i < this.$store.state.currentForm.catalogue.length; i++) {
        let list = this.$store.state.currentForm.catalogue[i];
        if (list.type == "category") {
          catName = list.name;
        }
        if (list.type == "item" && list.category == undefined) {
          list.category = catName;
        }
      }
      let data = this.$store.state.currentForm.catalogue.filter(
        (o) => o.type == "category" && o.name != ""
      );
      this.no_category_list = this.$store.state.currentForm.catalogue.filter(
        (o) => o.category == ""
      );
      if (data.length != 0) {
        if (data[0].items == undefined) {
          data.forEach((list) => {
            list.items = [];
            list.items = this.$store.state.currentForm.catalogue.filter(
              (o) => o.category == list.name
            );
          });
          this.category_list = data;
        } else if (data[0].items != undefined) {
          data.forEach((each_data) => {
            each_data.items = [];
            this.$store.state.currentForm.catalogue.forEach((each_item) => {
              if (each_item.type == "item") {
                if (each_data.name == each_item.category)
                  each_data.items.push(each_item);
              }
            });
          });
          this.category_list = data;
        }
      }
    },
    delete_Category_submit: function(cat_item) {
      // debugger;
      let list = this.$store.state.currentForm.catalogue;
      for (var i = 0; i < list.length; i++) {
        if (list[i].category == cat_item.name) {
          // console.log(list)
          this.$store.state.currentForm.catalogue.splice(i, 1);
        }
      }
      for (var c_item = 0; c_item < list.length; c_item++) {
        if (list[c_item].pos == cat_item.pos) {
          // console.log(list)
          this.$store.state.currentForm.catalogue.splice(c_item, 1);
        }
      }
      this.saveCurrentForm();
    },
    categoryDragg: function(event) {
      if (event) {
        console.log('called');
        this.saveCurrentFormDragAndDrop(this.category_list, "catDrag");
      }
    },
    itemDraggnoCat: function(event) {
      if (event) {
        
        this.$store.state.currentForm.catalogue = [];
        var self = this;
        this.category_list.map(function(each_element) {
          each_element.items.map(function(each_item) {
            each_item.category = each_element.name;
            self.$store.state.currentForm.catalogue.push(each_item);
          });
          self.$store.state.currentForm.catalogue.push(each_element);
        });
        this.no_category_list.map(function(each_element) {
          each_element.category = "";
          self.$store.state.currentForm.catalogue.push(each_element);
        });
        this.saveCurrentFormDragAndDrop(
          self.$store.state.currentForm.catalogue,
          "itemDragCat"
        );
      }
    },
    itemDraggg: function(event) {
      if (event) {
        this.$store.state.currentForm.catalogue = [];
        var self = this;
        this.category_list.map(function(each_element) {
          each_element.items.map(function(each_item) {
            each_item.category = each_element.name;
            self.$store.state.currentForm.catalogue.push(each_item);
          });
          self.$store.state.currentForm.catalogue.push(each_element);
        });
        this.no_category_list.map(function(each_element) {
          each_element.category = "";
          self.$store.state.currentForm.catalogue.push(each_element);
        });
        this.saveCurrentFormDragAndDrop(
          self.$store.state.currentForm.catalogue,
          "itemDragCat"
        );
      }
    },
    saveCurrentFormDragAndDrop: function(category_list, type) {
      //this.disableDraggableCategory = true;
      var cat_list;
      if (type == "itemDragCat") {
        this.$store.state.currentForm.catalogue = category_list;
      } else {
        cat_list = category_list;
        if (this.$store.state.currentForm.catalogue.length > cat_list.length) {
          for (
            var r = 0;
            r < this.$store.state.currentForm.catalogue.length;
            r++
          ) {
            if (this.$store.state.currentForm.catalogue[r].type == "item") {
              cat_list.push(this.$store.state.currentForm.catalogue[r]);
            }
          }
          this.$store.state.currentForm.catalogue = cat_list;
        } else {
          cat_list.maxlength = this.$store.state.currentForm.catalogue.maxlength;
          this.$store.state.currentForm.catalogue = cat_list;
        }
        this.$store.state.currentForm.catalogue.maxlength = cat_list.maxlength;
      }
      var elethis = this;
      this.$store.dispatch("setOverlay", true);
      elethis.set_overlay = true;
      saveFile(
        this.$store.state.profile.uid,
        this.$store.state.currentFormPos,
        JSON.stringify(this.$store.state.currentForm),
        function(error) {
          if (error) {
            //TODO skeleton data
            alert(i18n.t("alertText.saveFailed"));
            store.dispatch("setOverlay", false);
            elethis.set_overlay = false;
          } else {
            elethis.category_lists();
            store.dispatch("setOverlay", false);
            elethis.set_overlay = false;
          }
        }
      );
      saveMasterData(function() {}, this.$store.state);
      // }
    },
    saveCurrentForm: function() {
      // console.log(this.$store.state.currentForm.catalogue)
      // var shopname = this.$store.state.currentForm.shopname;
      // var phone = this.$store.state.currentForm.phone;
      // console.log(this.$store.state);
      // if (shopname == null || !shopname || shopname == "") {
      //     alert(i18n.t("alertText.validateNameAlert", { type: this.type }));
      // } else if (phone.length != 13) {
      //     alert(i18n.t("alertText.validatePhoneAlert"));
      // } else {
      var elethis = this;
      this.$store.dispatch("setOverlay", true);
      elethis.set_overlay = true;
      saveFile(
        this.$store.state.profile.uid,
        this.$store.state.currentFormPos,
        JSON.stringify(this.$store.state.currentForm),
        function(error) {
          if (error) {
            //TODO skeleton data
            alert(i18n.t("alertText.saveFailed"));
            store.dispatch("setOverlay", false);
            elethis.set_overlay = false;
          } else {
            alert(i18n.t("alertText.uploadText"));
            store.dispatch("setOverlay", false);
            elethis.set_overlay = false;
            router.go();
          }
        }
      );
      saveMasterData(function(err) {
        console.log(err);
      }, this.$store.state);
      // }
      // console.log(this.$store.state.currentForm.catalogue)
    },
  },
  mounted() {
    setLocale();
    this.category_lists();
  },
};
</script>
<style lang="scss">
.bottom_modal {
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.text_bold {
  font-weight: 500;
}
.dragButton {
  color: white !important;
  font-weight: bold;
  background: #8ab26d !important;
  outline: none;
}
@media (max-width: 960px) {
  .grid-container2 {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 2px;
  }
  .item_category {
    padding: 5px;
    margin: 12px 0px;
    color: #fff;
    background-color: #8ab26d;
    border-radius: 5px;
  }
  .mobile_dragclass {
    background-color: #666666;
    color: #fff;
  }
  .mobile_side_padding {
    padding: 0px 25px;
  }
  .landscape_side_padding {
    padding: 0px 55px;
  }
}
@media (min-width: 960px) {
  .item_category {
    padding: 5px;
    margin: 12px 0px;
    color: #fff;
    background-color: #31333e;
    border-radius: 5px;
  }
  .grid-container2 {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
    grid-gap: 5px;
  }
  .grid-container2_n {
    display: grid;
    grid-gap: 5px;
  }
  .borderClass {
    background-color: #666666;
    color: #fff;
  }
}
@media (min-width: 960px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0.2em;
  }
  .grid_n {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 0.2em;
  }
  .viewform_images_list {
    width: 100%;
  }
}

.ghost-card {
  opacity: 0.5;
  background: #f7fafc;
  border: 1px solid #4299e1;
}
</style>
