eslint-disable vue/no-async-in-computed-properties
<template>
  <section>
    <AdminHeader :headerName="payload.shopname" :view_form="true" />

    <div class="desktop_margin">
      <div style="display:flex;justify-content: flex-end;">
        <selectLocale
          style="color: #94b978;margin-top:15px;    margin-right: 15px; "
        />
      </div>
      <v-overlay :value="$store.state.overlay">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
      <div class="mobileview background_color">
        <div class="text-center text_color alignment_longText">
          {{ payload.shopdesc }}
        </div>
        <div class="text-center mobile_subheader_text mobileview">
          {{ $t("newUi.adminViewform.whatsappNumber") }}: {{ payload.phone }}
        </div>
        <div
          class="text-center mobileview mobile_subheader_text"
          v-show="$store.state.currentForm.currency"
        >
          {{ $t("newUi.adminViewform.currency") }}
          :
          {{
            $store.state.currentForm.currency +
              " - " +
              $store.state.currentForm.symbol
          }}
        </div>
        <div
          class="text-center mobileview mobile_subheader_text"
          v-show="$store.state.currentForm.createdOn"
        >
          {{ $t("newUi.adminViewform.dateTitle") }}:
          {{ created_date }}
        </div>

        <!-- <div class="text-center desktopView" style="font-size: 12px;">WhatsApp number:{{payload.phone}}  |  Created on:{{created_date}}</div> -->
      </div>
      <span style="display: none"
        >{{ data_changed }} {{ prod_data_changed }}</span
      >
      <v-container class="desktopView pt-10">
        <div class="d-flex justify-space-between">
          <div
            class="text-left justify-start"
            style="max-width: 400px; width: 100%"
          >
            <div class="cursor-pointer header-text" @click="editform()">
              {{ payload.shopname }}
              <img
                class="cursor-pointer"
                src="../assets/ui/edit_icon.svg"
                alt=""
                style="background-color: transparent"
              />
            </div>
          </div>
          <div class="justify-end" style="font-size: 12px">
            <div class="row p-2 justify-content-end" style="width:500px">
              <div class="link_heading pt-3 marg-10">
                {{ $t("newUi.adminViewform.linkButton") }}
              </div>
              <div class="col-7 background_color1 text-truncate text_color">
                {{ shareablelink }}
              </div>
              <span class="pt-2" style="background: #f8faf7;">
                <img
                  class="icon_color cursor-pointer"
                  src="../assets/ui/share.svg"
                  alt=""
                  @click="share_dialog = true"
                  style="background: transparent"
                />
              </span>
            </div>
          </div>
        </div>
      </v-container>
      <v-container class="desktopView">
        <div class="background_color">
          <!-- <div class="text_color">{{ payload.shopdesc }}</div> -->
          <div class="d-flex justify-space-between align-items-end">
            <div class="justify-start text_color alignment_longText">
              {{ payload.shopdesc }}
            </div>
            <div
              class="justify-end created-on"
              v-show="$store.state.currentForm.currency"
            >
              {{ $t("newUi.adminViewform.currency") }}:
              {{
                $store.state.currentForm.currency +
                  " - " +
                  $store.state.currentForm.symbol
              }}
            </div>
          </div>
          <div class="d-flex justify-space-between">
            <div class="justify-start whatText">
              {{ $t("newUi.adminViewform.whatsappNumber") }}:
              {{ payload.phone }}
            </div>
            <div class="justify-end created-on">
              {{ $t("newUi.adminViewform.dateTitle") }}:
              {{ created_date }}
            </div>
          </div>
        </div>
        <!-- <div class="text-center desktopView" style="font-size: 12px;">WhatsApp number:{{payload.phone}}  |  Created on:{{created_date}}</div> -->
      </v-container>
      <v-container
        class="link_heading mobileview mobile_side_padding"
        :class="{ landscape_side_padding: $vuetify.breakpoint.height < 450 }"
      >
        {{ $t("newUi.adminViewform.linkButton") }}
      </v-container>

      <v-container
        class=" mobileview mobile_side_padding"
        :class="{ landscape_side_padding: $vuetify.breakpoint.height < 450 }"
      >
        <div class="d-flex">
          <div class="col-11 background_color1 text-truncate text_color">
            {{ shareablelink }}
          </div>
          <span>
            <img
              class="icon_color cursor-pointer ma-1"
              src="../assets/ui/share.svg"
              alt=""
              @click="share_dialog = true"
              style="background-color: transparent"
            />
          </span>
        </div>
      </v-container>

      <v-container
        class="desktopView"
        style="padding: 2px"
        v-if="payload.catalogue.length != 0"
      >
        <div class="d-flex justify-content-end">
          <v-btn
            v-bind:class="{ displayNone: !disableDraggableCategory }"
            @click="add_category_desktop"
            class="button_padding cursor-pointer"
            style="font-size: 12px"
            color="#8AB26D"
            >{{ $t("newUi.adminViewform.addCat") }} ({{
              payload.catalogue.filter((o) => o.type == "category").length
            }})</v-btn
          >
          <v-btn
            v-bind:class="{ displayNone: !disableDraggableCategory }"
            @click="add_product_desktop"
            class="button_padding cursor-pointer"
            style="font-size: 12px"
            color="#8AB26D"
          >
            {{ $t("newUi.adminViewform.addPro") }} ({{
              payload.catalogue.filter((o) => o.type == "item").length
            }})</v-btn
          >
          <v-btn
            v-bind:class="{ displayNone: !disableDraggableCategory }"
            @click="dragCat"
            class="button_padding cursor-pointer"
            style="font-size: 12px"
            color="#8AB26D"
          >
            {{ $t("newUi.adminViewform.reOrder") }}</v-btn
          >
          <v-btn
            v-bind:class="{ displayNone: disableDraggableCategory }"
            @click="dragCat"
            class="button_padding cursor-pointer"
            style="font-size: 10px"
            color="#8AB26D"
          >
            {{ $t("newUi.adminViewform.desableReorder") }}</v-btn
          >
        </div>
      </v-container>
      <FormList
        v-if="
          state.profile.isSigned &&
            $store.state.currentForm.catalogue.length != 0
        "
        v-bind:payload="payload"
        :draggableList="disableDraggableCategory"
        ref="nameOfRef"
        class="mb-12"
      ></FormList>

      <v-container
        class="mobile_data mt-10"
        v-if="
          $store.state.currentForm.catalogue.length == 0 &&
            $store.state.fileLoaded == true &&
            $store.state.overlay == false
        "
      >
        <div class="desktop_box">
          <div>
            <img
              class="icon_color cursor-pointer ma-1"
              src="../assets/ui/no_form.svg"
              alt=""
              style="background-color: transparent; width: 4rem; height: 4rem"
            />
          </div>
          <div class="mt-3">
            {{ $t("newUi.zeroForms.noProducts") }}
          </div>
          <!-- <div>{{ $t("newUi.zeroForms.getStarted") }}</div> -->
        </div>
        <div class="desktopView mt-10">
          <v-btn
            @click="add_category_desktop"
            class="button_padding cursor-pointer"
            style="font-size: 10px"
            color="#8AB26D"
            >{{ $t("newUi.adminViewform.addCat") }} ({{
              payload.catalogue.filter((o) => o.type == "category").length
            }})</v-btn
          >
          <v-btn
            @click="add_product_desktop"
            class="button_padding cursor-pointer"
            style="font-size: 10px"
            color="#8AB26D"
          >
            {{ $t("newUi.adminViewform.addPro") }} ({{
              payload.catalogue.filter((o) => o.type == "item").length
            }})</v-btn
          >
        </div>
      </v-container>
      <v-btn
        @click="dragCat"
        class="mobileview  button_padding reorder-desable-button cursor-pointer"
        v-bind:class="{ displayNone: disableDraggableCategory }"
        style="font-size: 10px"
        color="#8AB26D"
      >
        {{ $t("newUi.adminViewform.desableReorder") }}
      </v-btn>
      <v-menu
        top
        left
        v-bind:class="{ displayNone: !disableDraggableCategory }"
      >
        <template v-slot:activator="{ on, attrs }">
          <div
            class="cursor-pointer plus_button_icon"
            v-bind="attrs"
            v-on="on"
            v-bind:class="{ displayNone: !disableDraggableCategory }"
            @click="open_add_menu != open_add_menu"
          >
            <img
              class="cursor-pointer plus_icon_white"
              src="../assets/ui/plus_icon.svg"
              alt=""
            />
          </div>
          <!-- <v-btn
            class="mobileview"
            style="border: none;"
             v-bind:class="{displayNone : !disableDraggableCategory}"
            color="#8AB26D"
            v-bind="attrs"
            v-on="on"
            v-if="state.profile.isSigned"
            elevation="6"
            fab
            fixed
            right
            @click="open_add_menu != open_add_menu"
            bottom
          >
            <img
              class="cursor-pointer"
              src="../assets/ui/plus_icon.svg"
              alt=""
            />
          </v-btn> -->
        </template>

        <v-list
          class="mobileview"
          style="background:transparent"
          v-bind:class="{ displayNone: !disableDraggableCategory }"
        >
          <v-list-item class="list-box">
            <v-list-item-title
              class="text_color d-flex list-style cursor-pointer"
              style="padding: 10px; background-color: #fff;"
              @click="open_add_product_modal()"
            >
              <img
                class="cursor-pointer plus_button_image_padding"
                src="../assets/ui/add_product.svg"
                alt=""
              />
              <span style="align-self: center">
                {{ $t("newUi.adminViewform.addPro") }} ({{
                  payload.catalogue.filter((o) => o.type == "item").length
                }})</span
              ></v-list-item-title
            >
          </v-list-item>
          <v-list-item class="list-box">
            <v-list-item-title
              class="text_color d-flex list-style cursor-pointer"
              style="padding: 10px; background-color: #fff;"
              @click="open_add_category_modal()"
            >
              <img
                class="cursor-pointer plus_button_image_padding"
                src="../assets/ui/add_category.svg"
                alt=""
              />
              <span style="align-self: center"
                >{{ $t("newUi.adminViewform.addCat") }} ({{
                  payload.catalogue.filter((o) => o.type == "category").length
                }})</span
              ></v-list-item-title
            >
          </v-list-item>
          <v-list-item class="list-box">
            <v-list-item-title
              class="text_color d-flex list-style cursor-pointer"
              style="padding: 10px; background-color: #fff;"
              @click="dragCat"
            >
              <img
                class="cursor-pointer plus_button_image_padding"
                src="../assets/ui/reorder_icon.svg"
                alt=""
              />
              <span style="align-self: center">
                {{ $t("newUi.adminViewform.reOrder") }}</span
              ></v-list-item-title
            >
          </v-list-item>
        </v-list>
      </v-menu>
      <v-bottom-sheet v-model="add_product">
        <!-- <v-sheet  > -->
        <v-card class="elevation-0 bottom_modal">
          <v-card-text
            class="text-center bottom_modal"
            style="overflow-y: scroll;"
            :class="{
              sheet_height: $vuetify.breakpoint.height < 450,
              sheet_normal_height: $vuetify.breakpoint.height > 450,
            }"
          >
            <AddProducts :payload="payload"></AddProducts>
          </v-card-text>
        </v-card>
        <!-- </v-sheet> -->
      </v-bottom-sheet>
      <v-bottom-sheet v-model="add_category">
        <!-- <v-sheet class="text-center bottom_modal" height="250px">
          <AddCategory :payload="payload"></AddCategory>
        </v-sheet> -->
        <v-card class="elevation-0 bottom_modal">
          <v-card-text
            class="text-center "
            style="overflow-y: scroll; bottom-padding: 40px;"
            :class="{ sheet_height: $vuetify.breakpoint.height < 450 }"
          >
            <AddCategory :payload="payload"></AddCategory>
            <div
              :class="{ padding_category: $vuetify.breakpoint.height < 450 }"
            ></div>
          </v-card-text>
        </v-card>
      </v-bottom-sheet>
      <v-dialog v-model="share_dialog" content-class="crop-dialog">
        <v-container style="height: 100px; background-color: #fff">
          <div class="d-flex justify-space-between text_color">
            <div class="d-flex justyfy-start">
              {{ $t("newUi.addCategory.shareLink") }}
            </div>
            <div class="d-flex justify-end" style="font-size: 14px">
              <span class="cursor-pointer" @click="share_dialog = false"
                >x</span
              >
            </div>
          </div>
          <div class="d-flex mt-3">
            <social-share
              :share_url="shareablelink"
              :shopname="payload.shopname"
              :desc="payload.shopdesc"
            ></social-share>
            <div class="do_copy" @click="doCopy">
              <img
                class="do_copy_icon cursor-pointer"
                src="../assets/ui/copy.svg"
                alt=""
              />
            </div>
          </div>
        </v-container>
      </v-dialog>
    </div>
  </section>
</template>
<script>
// @ is an alias to /src
import FormList from "@/components/FormList.vue";
import AdminHeader from "@/layout/adminHeader.vue";
import router from "../router/router";
import { mapGetters } from "vuex";
import { setLocale, urlShorter } from "../services/firebase";
import { loadFile } from "../services/firebase";
import moment from "moment";
import selectLocale from "../components/selectLocale";
import AddProducts from "@/components/AddProducts.vue";
import AddCategory from "@/components/AddCategory.vue";
import i18n from "../i18n";
import SocialShare from "../components/socialShare.vue";

export default {
  data() {
    const category_list = [];
    return {
      shareablelink: "",
      name: "",
      add_product: false,
      showMessage: false,
      isEmpty: false,
      open_add_menu: false,
      add_category: false,
      category_list,
      share_dialog: false,
      cat_flag: false,
      prod_flag: false,
      disableDraggableCategory: true,
      createdDate: new Date(this.$store.state.currentForm.createdOn),
    };
  },
  components: {
    AdminHeader,
    FormList,
    AddProducts,
    AddCategory,
    SocialShare,
    selectLocale,
  },
  props: ["payload"],
  mounted() {
    if (!this.$store.state.profile.isSigned) {
      router.push("/");
    } else {
      setLocale();
      this.$store.dispatch("fileLoaded", false);
      this.fetchData();
    }
  },
  updated() {
    this.$nextTick(function() {
      this.linkval;
    });
  },
  watch: {
    $route: "fetchData",
  },
  computed: {
    ...mapGetters({
      user: "user",
      state: "state",
    }),

    created_date: function() {
      const created_month = this.createdDate.toLocaleString("default", {
        month: "short",
      });
      var month,
        date = this.createdDate.getDate(),
        year = this.createdDate.getFullYear(),
        formate_date;

      Object.keys(i18n.t(`months`)).map((monthName, i) => {
        if (created_month == monthName) {
          month = i18n.t(`months`)[monthName];
        }
      });
      if (!isNaN(date) && month != undefined && !isNaN(year)) {
        formate_date = date + " " + month + " " + year;
      }

      return formate_date;
    },
    data_changed: function() {
      if (this.cat_flag == true) {
        if (this.add_category == false) {
          if (
            this.cat_count != this.$store.state.currentForm.catalogue.maxlength
          ) {
            router.go();
          }
        }
      }
      return null;
    },
    prod_data_changed: function() {
      if (this.prod_flag == true) {
        if (this.add_product == false) {
          if (
            this.prod_count != this.$store.state.currentForm.catalogue.maxlength
          ) {
            router.go();
          }
        }
      }
      return null;
    },
    linkval: async function() {
      // store.dispatch("setOverlay", true);

      // this.$store.state.all_store.filter(each_store=>{
      //   if(each_store.store_name.replace(/_/g," ")==this.$store.state.currentForm.shopname)
      //   {
      //     this.$store.state.current_store_id=each_store.storeId;
      //   }})
      // var URL=`${process.env.VUE_APP_GIP_RETAIL}/${this.$store.state.current_store_id}`;
      // return URL;

      var savedonce = false;
      var mypos1 = parseInt(this.$route.params.formno + "");
      // var mypos = parseInt(this.payload.pos + "");
      // for (var i = 0; i < this.$store.state.forms.length; i++) {
      //   var f = this.$store.state.forms[i];
      //   //var fpos = parseInt(f.link.split("/")[3]);
      //   var fpos;
      //   if (f.link.split("/")[3]) {
      //     fpos = parseInt(f.link.split("/")[3]);
      //   } else {
      //     fpos = parseInt(f.link.split("/")[2]);
      //   }
      //   if (fpos === mypos) savedonce = true;
      // }
      if (mypos1 == this.$store.state.currentFormPos) savedonce = true;
      if (savedonce) {
        var url = window.location.protocol + "//" + location.host;
        var language = this.$store.state.locale
          ? this.$store.state.locale
          : "en";
        var result = "";
        if (location.host.includes("localhost")) {
          // result = await urlShorter(
          //   "https://qa-sellwell.gupshup.io" +
          //     "/form/" +
          //     this._props.payload.id +
          //     "/" +
          //     this._props.payload.pos +
          //     "?lang=" +
          //     language
          // );
          result =
            url +
            "/form/" +
            this._props.payload.id +
            "/" +
            this.$store.state.currentFormPos +
            "?lang=" +
            language;
          this.shareablelink = result;
          // store.dispatch("setOverlay", false);
        } else {
          result = await urlShorter(
            url +
              "/form/" +
              this._props.payload.id +
              "/" +
              this.$store.state.currentFormPos +
              "?lang=" +
              language
          );
          this.shareablelink = result;
          // store.dispatch("setOverlay", false);
        }
      } else {
        this.shareablelink = "";
        // store.dispatch("setOverlay", false);
      }
    },
  },
  methods: {
    dragCat: function() {
      this.disableDraggableCategory = !this.disableDraggableCategory;
    },
    fetchData: function() {
      var store = this.$store;
      store.dispatch("setOverlay", true);
      store.dispatch("setCurrentFormData", {
        id: store.state.profile.uid,
        pos: "",
        categs: ["Ordering form", "Catalogue"],
        shopname: "",
        shopdesc: "",
        type: "",
        catalogue: [],
        phone: "",
      });
      this.$store.state.currentFormPos = this.$route.params.formno;
      const disele = this;

      loadFile(
        disele.$store.state.profile.uid,
        disele.$route.params.formno,
        disele.$store.state,
        function(error) {
          if (error) {
            //TODO skeleton data
            store.dispatch("setCurrentFormData", {
              id: store.state.profile.uid,
              pos: store.state.currentFormPos,
              categs: ["Ordering form", "Catalogue"],
              shopname: "",
              shopdesc: "",
              type: "Ordering form",
              catalogue: [],
              phone: "+919000000000",
            });
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          } else {
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          }
        }
      );
    },
    doCopy: function() {
      console.log("Hi doCopy");
      console.log("navigator in view form", navigator);
      var text = this.shareablelink;
      if (navigator || navigator != undefined) {
        navigator.clipboard
          .writeText(text)
          .then(() => {
            this.$store.state.snackbartext = i18n.t("common.copy");
            this.$store.state.snackbar = true;
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        var textField = document.createElement("textarea");
        textField.innerText = text;
        document.body.appendChild(textField);
        textField.select();
        textField.focus(); //SET FOCUS on the TEXTFIELD
        console.log("textField", textField);
        try {
          var successful = window.document.execCommand("copy");
          console.log("successful", successful);
          if (successful) {
            textField.remove();
            this.$store.state.snackbartext = i18n.t("common.copy");
            this.$store.state.snackbar = true;
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    editform: function() {
      router.push("/editform/" + this.$store.state.currentFormPos);
    },
    add_product_desktop() {
      router.push({
        name: "addItems",
        params: {
          formno: this.$store.state.currentForm.pos,
          add_type: "Product",
        },
      });
    },

    add_category_desktop() {
      router.push({
        name: "addItems",
        params: {
          formno: this.$store.state.currentForm.pos,
          add_type: "Category",
        },
      });
    },
    open_add_category_modal() {
      this.add_category = !this.add_category;
      if (this.add_category == true) {
        this.cat_count = this.$store.state.currentForm.catalogue.maxlength;
        this.cat_flag = true;
      }
    },
    open_add_product_modal() {
      this.add_product = !this.add_product;
      if (this.add_product == true) {
        this.prod_count = this.$store.state.currentForm.catalogue.maxlength;
        this.prod_flag = true;
      }
    },
    generate_list: function() {
      this.category_list = this.payload.catalogue.filter(
        (o) => o.type == "category"
      );
      this.category_list.forEach((list) => {
        list.items = [];
        list.items = this.payload.catalogue.filter(
          (o) => o.category == list.name
        );
      });
    },
  },
};
</script>
<style lang="scss">
.icon_color {
  color: #8ab26d;
  justify-content: center;
  font-size: 20px;
  width: 26px;
  height: 26px;
  background-color: #f8faf7;
  padding: 5px;
  margin: 3px;
  border-radius: 3px;
}

.alignment_longText {
  word-break: break-all;
}

input[type="text"] {
  margin-bottom: 0px;
  border: none;
  margin-top: 0px;
}
.bottom_modal {
  border-top-right-radius: 20px !important;
  border-top-left-radius: 20px !important;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.text_color {
  color: #8ab26d;
  font-weight: bold;
}
@media (min-width: 320px) and (max-width: 960px) {
  .mobile_data {
    text-align: center;
  }
  .background_color {
    background-color: #f8faf7;
    padding: 10px;
    margin-bottom: 10px;
    width: 100%;
  }
  .whatsappText {
    text-align: left;
    font-size: 13px;
    letter-spacing: 0px;
    color: #7c7c7c;
    opacity: 1;
  }
  .background_color1 {
    background: #f8faf7 0% 0% no-repeat padding-box;
    font-size: 12px;
    opacity: 1;
  }
  .mobile_subheader_text {
    font-size: 12px;
    letter-spacing: 0px;
    color: #666666;
    opacity: 1px;
  }
  .do_copy {
    color: #fff;
    width: 38px;
