<template>
  <div>
    <div
      v-if="item.type != 'item'"
      class="d-flex justify-space-between item_category"
    >
      <div
        class=""
        style="padding-left: 10px; padding-top: 3px; padding-bottom: 2px"
      >
        {{ item.name }}
      </div>
      <div class="d-flex justify-content-end">
        <!-- <div 
              v-if="!draggable"
              class=" text-center horizontal-icon"
              style="padding-right:10px; font-size: 16px; width:14px; height:14px; font-weight: bold;"></div> -->
        <div>
          <v-menu bottom left>
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                v-on="on"
                v-bind:class="{
                  displayNone: !draggable,
                }"
                class="text-center horizontal-icon"
                style="font-size: 16px; padding-right: 10px; font-weight: bold"
              >
                ...
              </div>
            </template>
            <v-list>
              <v-list-item>
                <v-list-item-title
                  class="mobileview cursor-pointer"
                  style="padding: 5px"
                  @click="edit_category = true"
                  >{{ $t("newUi.adminViewform.edit") }}</v-list-item-title
                >
                <v-list-item-title
                  class="desktopView cursor-pointer"
                  style="padding: 5px"
                  @click="edit_category_desktop"
                  >{{ $t("newUi.adminViewform.edit") }}</v-list-item-title
                >
              </v-list-item>
              <v-list-item>
                <v-list-item-title
                  class="mobileview cursor-pointer"
                  style="padding: 5px"
                  @click="delete_category_show = true"
                  >{{ $t("newUi.adminViewform.delete") }}</v-list-item-title
                >
                <v-list-item-title
                  class="desktopView cursor-pointer"
                  style="padding: 5px"
                  @click="delete_category_dialog = true"
                  >{{ $t("newUi.adminViewform.delete") }}</v-list-item-title
                >
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
        <div>
          <img
            v-if="!draggable"
            @click="dragCategory"
            @mouseover="dragCategory"
            @mouseout="stopDrag"
            src="../assets/ui/drag.svg"
            style="cursor: grab"
          />
          <!-- <img
          v-if="!isMobile"
            @click="dragCategory"
            @mouseover="dragCategory"
            @mouseout="stopDrag"
            src="../assets/ui/drag.svg"
            style="cursor: grab"
          /> -->
        </div>
      </div>
    </div>
    <v-bottom-sheet v-model="delete_category_show">
      <v-sheet class="text-center bottom_modal" height="300px">
        <div class="pt-8 text_color">
          {{ $t("newUi.deleteCategory.deleteTitle") }}
        </div>
        <v-container>
          <div class="col">{{ $t("newUi.deleteCategory.deleteConfirm") }}</div>
          <div class="col">
            {{ $t("newUi.deleteCategory.deleteCat") }}
          </div>
          <div class="text-center mt-3">
            <v-btn
              @click="cancel_delete()"
              class="button_padding cursor-pointer"
              color="#8AB26D"
            >
              {{ $t("newUi.deleteCategory.cancel") }}
            </v-btn>
            <v-btn
              @click="delete_Category_submit(item)"
              class="button_padding cursor-pointer"
              color="#8AB26D"
              >{{ $t("newUi.deleteCategory.confirm") }}</v-btn
            >
            <!-- <div @click="add_category_more(name, description)" class="text-decoration-underline cursor text_color">save and add more</div> -->
          </div>
        </v-container>
      </v-sheet>
    </v-bottom-sheet>
    <v-dialog v-model="delete_category_dialog" content-class="delete_dialog">
      <v-container class="delete_dialog">
        <!-- <div class="d-flex justify-space-between text_color"> -->
        <div
          class="d-flex justify-end justify-content-end text_color"
          style="font-size: 14px"
        >
          <span class="cursor-pointer" @click="delete_category_dialog = false"
            >x</span
          >
        </div>
        <!-- </div> -->
        <div class="text-center text_color">
          {{ $t("newUi.deleteCategory.deleteTitle") }}
        </div>
        <div class="text-center">
          {{ $t("newUi.deleteCategory.deleteConfirm") }}
        </div>
        <div class="text-center">
          {{ $t("newUi.deleteCategory.deleteCat") }}
        </div>
        <div class="text-center mt-1">
          <v-btn
            @click="delete_category_dialog = false"
            class="button_padding cursor-pointer"
            color="#8AB26D"
            >{{ $t("newUi.deleteCategory.cancel") }}</v-btn
          >
          <v-btn
            @click="delete_Category_submit(item)"
            class="button_padding cursor-pointer"
            color="#8AB26D"
            >{{ $t("newUi.deleteCategory.confirm") }}</v-btn
          >
          <!-- <div @click="add_category_more(name, description)" class="text-decoration-underline cursor text_color">save and add more</div> -->
        </div>
      </v-container>
    </v-dialog>
    <v-bottom-sheet v-model="edit_category">
      <v-card class="elevation-0 bottom_modal">
        <v-card-text
          class="text-center "
          style="overflow-y: scroll;"
          :class="{ sheet_height: $vuetify.breakpoint.height < 450 }"
        >
          <EditCategory :payload="item"></EditCategory>
          <div
            :class="{ padding_category1: $vuetify.breakpoint.height < 450 }"
          ></div>
        </v-card-text>
      </v-card>
    </v-bottom-sheet>
  </div>
</template>
<script>
import router from "../router/router";
import { saveFile,loadFile } from "../services/firebase";
import { saveMasterData } from "../services/firebase";
import EditCategory from "./EditCategory.vue";
import store from "../store/store";
import i18n from "../i18n";
import { setLocale } from "../services/firebase";

export default {
  props: [
    "item",
    "payload",
    "dragCat",
    "draggable",
    "dragCategory",
    "stopDrag",
    "isMobile",
  ],
  components: {
    EditCategory,
  },
  data() {
    var category_list = [];
    return {
      dialog: false,
      edit_product: false,
      edit_category: false,
      delete_category_show: "",
      delete_category_dialog: false,
      category_list,
    };
  },

  methods: {
    category_lists: function() {
      let data = this.$store.state.currentForm.catalogue.filter(
        (o) => o.type == "category"
      );
      data.forEach((list) => {
        list.items = [];
        list.items = this.$store.state.currentForm.catalogue.filter(
          (o) => o.category == list.name
        );
      });
      this.category_list = data;
    },
    fetchData: function() {
      var store = this.$store;
      store.dispatch("setOverlay", true);
      store.dispatch("setCurrentFormData", {
        id: store.state.profile.uid,
        pos: "",
        categs: ["Ordering form", "Catalogue"],
        shopname: "",
        shopdesc: "",
        type: "",
        catalogue: [],
        phone: "",
      });
      this.$store.state.currentFormPos = this.$route.params.formno;
      const disele = this;
      loadFile(
        disele.$store.state.profile.uid,
        disele.$route.params.formno,
        disele.$store.state,
        function(error) {
          if (error) {
            //TODO skeleton data
            console.log('error');
            store.dispatch("setCurrentFormData", {
              id: store.state.profile.uid,
              pos: store.state.currentFormPos,
              categs: ["Ordering form", "Catalogue"],
              shopname: "",
              shopdesc: "",
              type: "Ordering form",
              catalogue: [],
              phone: "+919000000000",
            });
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          } else {
            store.dispatch("fileLoaded", true);
            disele.linkval;
            store.dispatch("setOverlay", false);
          }
        }
      );
    },
    delete_Category_submit: function(cat_item) {
      this.$store.dispatch("setOverlay", true);
      this.delete_category_dialog = false;
      let list = this.$store.state.currentForm.catalogue;
      for (var i = 0; i < list.length; i++) {
        if (list[i].category == cat_item.name) {
          list.splice(i, 1);
          i--;
        }
      }
      for (var c_item = 0; c_item < list.length; c_item++) {
        if (list[c_item].pos == cat_item.pos) {
          list.splice(c_item, 1);
        }
      }
      this.saveCurrentForm();
    },
    cancel_delete() {
      this.delete_category_show = false;
    },
    edit_category_desktop() {
      router.push({
        name: "addItems",
        params: {
          formno: this.$store.state.currentForm.pos,
          item_deatails: this.item,
          edit_type: "edit_category",
        },
      });
    },
    edit_Category_submit: function(cat_item) {
      //   debugger;
      // let data = this.$store.state.currentForm.catalogue.filter(o => o.pos == cat_item_pos)
      let list = this.$store.state.currentForm.catalogue;
      for (var i = 0; i < list.length; i++) {
        if (list[i].category == cat_item.name) {
          this.$store.state.currentForm.catalogue.splice(i, 1);
        }
      }
      for (var c_item = 0; c_item < list.length; c_item++) {
        if (list[c_item].pos == cat_item.pos) {
          this.$store.state.currentForm.catalogue.splice(c_item, 1);
        }
      }
      this.saveCurrentForm();
    },
    saveCurrentForm: function() {
      saveFile(
        this.$store.state.profile.uid,
        this.$store.state.currentFormPos,
        JSON.stringify(this.$store.state.currentForm),
        function(error) {
          if (error) {
            //TODO skeleton data
            alert(i18n.t("alertText.saveFailed"));
            store.dispatch("setOverlay", false);
          }
        }
      ).then(()=>{
        saveMasterData(function(err) {
        console.log(err);
      }, this.$store.state);
      this.fetchData();  
      })
      this.$store.dispatch("setOverlay", false);
    },
  },
  mounted() {
    setLocale();
    this.category_lists();
  },
};
</script>
<style lang="scss">
.bottom_modal {
  border-top-right-radius: 20px !important;
  border-top-left-radius: 20px !important;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.text_bold {
  font-weight: 500;
}
.displayNone {
  display: none;
}
.text_color {
  color: #8ab26d;
  font-weight: bold;
}
.delete_dialog {
  height: 180px;
  width: 400px !important;
  background-color: #fff;
}
@media (min-width: 960px) {
  .item_category {
    padding: 5px;
    margin: 12px 0px;
    color: #fff;
    background-color: #31333e;
    border-radius: 5px;
  }
}
@media (max-width: 960px) {
  .item_category {
    padding: 5px;
    margin: 12px 0px;
    color: #fff;
    background-color: #8ab26d;
    border-radius: 5px;
  }
  .padding_category1 {
    height: 130px;
  }
}
</style>
